<template>
  <div v-if="isV">
    <div>
      <el-dialog title="" :visible.sync="setDialog"
       style="width: 70%;margin: 0 auto;" :show-close="false" :close-on-click-modal="false">
        {{setName}}<br>
        <!---->
        <el-radio-group v-model="vi" style="margin-top: 25px;" class="setRadio">
          <el-radio :label="0"></el-radio><br>
          <el-radio :label="2"></el-radio><br>
          <el-radio :label="1"></el-radio><br>
          <el-radio :label="3" v-if="isTeam"></el-radio>
        </el-radio-group>
        <!---->
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancelForm"> </el-button>
          <el-button type="primary" @click="submitForm"> </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
  import {changeVisibility} from "../api/api"
  export default {
    name: "setDialog",
    props: {
      setDialog: {
        default: false
      },
      setId: {
        default: false
      },
      Visi: {
        default: 3
      },
      isTeam:{
        default: false
      },
      setName:{
        default:[]
      }
    },
    watch:{
      setDialog(val){
        this.isV = val
      },
      Visi(val){
        this.vi = val
      }
    },
    data(){
      return {
        isV: false,
        vi: -1,
      }
    },
    methods:{
      cancelForm(){
        this.$emit('changeVisible', false, this.setId)
      },
      submitForm(){
        console.log(this.vi)
        console.log(this.Visi)
        changeVisibility(this.setId, this.vi).then(res=>{
          if(res.status === 200){
            this.$message({message:'', type: 'info'})
            this.$emit('changeVisible', false, this.setId)
          }
        }).catch(e=>{if(e.response.status === 401)
          this.$message({message:e.response.data, type:'error'})
          this.$emit('changeVisible', false, this.setId)
        })
      },
    }
  }
</script>

<style>
  .setRadio .el-radio__label {
      font-size: 16px;
      padding-left: 10px;
  }
</style>