<template>
  <div class="docList">
    <div v-if="type === 'history'">
      <el-table
        :row-style="tableRowStyle"
        :header-cell-style="tableHeaderColor"
        ref="filterTable"
        :data="tableData"
        class="tabley"
        height="600"
        style="width: 100%">
        <template slot="empty" style="background-color: whitesmoke;">
          <img style="width: 100px;height: 100px;margin-top:20px;" :src="logo"><br>
          
        </template>
        <el-table-column
          prop="readTime"
          label=""
          sortable
          width="200"
          column-key="readTime"
          :filters="createTimeFilter('readTime')"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="docName"
          sortable
          width="350"
          label="">
          <template slot-scope="scope">
            <span  style="cursor: pointer;" @click="toDoc(scope.row.docId)">{{scope.row.docName}}</span>
            <div class="tableI">
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i @click="handleEdit(scope.$index, scope.row.docId)"
               class="el-icon-s-tools"></i></el-tooltip>
            <el-tooltip effect="dark" placement="bottom" :hide-after="800" :enterable="false">
              <span slot="content"><span v-if="scope.row.isCollected"></span></span>
            <i @click="changeColl(scope.row.docId)"
              v-if="scope.row.isCollected"
              class="el-icon-star-on"></i>
            <i @click="changeColl(scope.row.docId)"
              v-else
              class="el-icon-star-off"></i></el-tooltip>
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i @click="openFolderDialog(scope.row.docId)"
               class="el-icon-s-custom"></i></el-tooltip>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="builder"
          label=""
          sortable>
          <template slot-scope="scope">
            <span style="cursor: pointer;" @click="toUser(scope.row.builderId)">{{scope.row.builder}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div v-else-if="type === 'dustbin'">
      <el-table
        :row-style="tableRowStyle"
        :header-cell-style="tableHeaderColor"
        ref="filterTable"
        :data="tableData"
        class="tabley"
        height="600"
        style="width: 100%">
        <template slot="empty" style="background-color: whitesmoke;">
          <img style="width: 100px;height: 100px;margin-top:20px;" :src="logo"><br>
          
        </template>
        <el-table-column
          prop="docName"
          sortable
          label="">
          <template slot-scope="scope">
            {{scope.row.docName}}
            <div class="tableI">
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i
              @click="handleEdit(scope.$index, scope.row.docId)"
              class="el-icon-s-tools"></i></el-tooltip>
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i
              @click="recover(scope.row.docId)"
              class="el-icon-refresh-right"></i></el-tooltip></div>
          </template>
        </el-table-column>
        <el-table-column
          prop="deleteTime"
          label=""
          sortable
          column-key="deleteTime"
          :filters="createTimeFilter('deleteTime')"
          :filter-method="filterHandler"
        >
        </el-table-column>
      </el-table>
    </div>

    <div v-else>
      <el-table
        :row-style="tableRowStyle"
        :header-cell-style="tableHeaderColor"
        ref="filterTable"
        :data="tableData"
        class="tabley"
        height="600"
        style="width: 100%">
        <template slot="empty" style="background-color: whitesmoke;">
          <img style="width: 100px;height: 100px;margin-top:20px;" :src="logo"><br>
          
        </template>
        <el-table-column
          prop="name"
          sortable
          width="350"
          label="">
          <template slot-scope="scope">
            <span  style="cursor: pointer;" @click="toDoc(scope.row.docId)">{{scope.row.docName}}</span>
            <div class="tableI">
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i
              @click="handleEdit(scope.$index, scope.row.docId)"
              class="el-icon-s-tools"></i></el-tooltip>
            <el-tooltip effect="dark" placement="bottom" :hide-after="800" :enterable="false">
              <span slot="content"><span v-if="scope.row.isCollected"></span></span>
            <i
              @click="changeColl(scope.row.docId)"
              v-if="scope.row.isCollected"
              class="el-icon-star-on"></i>
            <i
              @click="changeColl(scope.row.docId)"
              v-else
              class="el-icon-star-off"></i></el-tooltip>
            <el-tooltip effect="dark" content="" placement="bottom" :hide-after="800" :enterable="false">
            <i
              @click="openFolderDialog(scope.row.docId)"
              class="el-icon-s-custom"></i></el-tooltip>
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="builder"
          label=""
          sortable>
          <template slot-scope="scope">
            <span style="cursor: pointer;" @click="toUser(scope.row.builderId)">{{scope.row.builder}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop="lastTime"
          label=""
          sortable
          width="200"
          column-key="lastTime"
          :filters="createTimeFilter('lastTime')"
          :filter-method="filterHandler"
        >
        </el-table-column>
        <el-table-column
          prop="lastUser"
          label=""
          sortable
          column-key="lastUser"
        >
          <template slot-scope="scope">
            <span style="cursor: pointer;" @click="toUser(scope.row.lastUserId)">{{scope.row.lastUser}}</span>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <el-dialog
      title=""
      :visible.sync="Dialog"
      width="30%"
      center>
      <div style="">
        <div><span style="width: 120px; display: inline-block;"></span>{{Info.docName}}</div>
        <div>
          <span style="width: 120px; display:inline-block;"></span>
          <span @click="toUser(Info.builderId)" style="cursor: pointer;">{{Info.builder}}</span>
        </div>
        <div><span style="width: 120px; display:inline-block;"></span>{{Info.create_time}}</div>
        <div><span style="width: 120px; display:inline-block;"></span>{{Info.modify_time}}</div>
        <el-divider></el-divider>
        <div v-if="type !== 'dustbin'">
          <div v-for="(item, index) in Info.coworkers" :key="index" class="coworkers">
            <el-avatar :src="item.userImg" :size="'small'" style="cursor: pointer;vertical-align: middle;" @click.native="toUser(item.userId)"></el-avatar>
            <span style="height: 28px; padding-right: 15px;margin-left: 10px; cursor:pointer;" @click="toUser(item.userId)">{{item.userName}}</span>
            <i class="el-icon-user" v-if="item.isBuilder"></i>
            <el-link type="danger" style="position: absolute; right: 15px; top: 10px;"
                     @click="checkMove(item)" v-if="isCo" :disabled="item.role===2">
              <span v-if="!item.isBuilder&&userId+''!==Info.builderId+''"></span>
              <span v-else-if="!item.isBuilder&&isCo"></span>
            </el-link>
          </div>
        </div>
        <el-divider v-if="type !== 'dustbin'"></el-divider>
        <div v-if="type !== 'dustbin'&&role+''===1+''">
          <el-button plain type="primary" @click="setP(Info.id)"></el-button>
        </div>
        <div style=" margin-top: 20px;" v-if="type !== 'dustbin'">
          <el-button plain type="success" @click="shareDialog(Info.id)"></el-button>
        </div>
        <div style=" margin-top: 20px;" v-if="role+''===1+''"><el-button plain type="danger" @click="Delete(Info.id)">
          <span v-if="type==='dustbin'"></span>
          </el-button>
        </div>
      </div>
    </el-dialog>

    <folder-dialog
        :dialog="folderDialog"
        @changeVisible="changeVisible"
        :doc-id="docId"
        :type="'coworker'" :name="NAME"></folder-dialog>

    <share-dialog :share-dialog="shareV" :share-id="shareId" @changeVisible="changeShare" :share-name="NAME"></share-dialog>
    <set-dialog :set-dialog="setV" :set-id="setId" @changeVisible="changeP" :visi="visi" :is-team="isTeam" :set-name="NAME"></set-dialog>
  </div>
</template>

<script>
  import {
    fetchRecentDocs,
    fetchDocInfo,
    fetchCoworkers,
    removeCoworker,
    fetchMyDocs,
    fetchTeamDocs,
    undoCollect, doCollect, fetchDustbin, fetchCollections, deleteForever, undoDelete, deleteDoc, fetchMyBuild
  } from "../api/api";
  import {GetTime} from "../main"
  import logo from '../assets/icon.png'

  export default {
    name: "docList",
    props:{
      type:{
        default: 'history'//'collection', 'build', 'dustbin', 'team'?
      },
      team:{
        default: ''
      }
    },
    mounted() {
      this.init()
    },
    data(){
      return {
        isCo:false,
        role:-1,
        userId:localStorage.userId,
        logo:logo,
        tableData: [
          {
            docId: 1,
            docName: '1',
            readTime: GetTime('2020-08-14T00:04:23.408300')
          },
          {
            docId: 2,
            docName: '2',
            readTime: GetTime('2020-08-15T00:04:23.408300')
          },
          {
            docId: 3,
            docName: '3',
            readTime: GetTime('2020-08-14T00:04:23')
          },
          {
            docId: 4,
            docName: '4',
            readTime: GetTime('2020-08-11T00:04:23.408300')
          },
        ],
        Dialog: false,
        Info:{
          id: 1,
          builder: 'Kelly',
          coworkers:[
            {
              userId: 1,
              userName: 'Kelly',
              isBuilder: true,
              userImg: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
            },
            {
              userId: 2,
              userName: 'Kelly2',
              isBuilder: false,
              userImg: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg'
            }
          ]
        },
        folderDialog: false,
        docId: '1',
        shareId: '',
        shareV: false,
        setV: false,
        setId: '2',
        visi: 3,
        NAME: '',
        isTeam: false,
      }
    },
    methods:{
      init(){
        if(this.type === 'history'){
          fetchRecentDocs().then(res=>{
            if(res.status === 200){
              this.tableData = []
              res.data.forEach(i=>{
                this.tableData.push({
                  docId: i.document.id,
                  docName:i.document.name,
                  readTime: GetTime(i.read_time),
                  isCollected: i.document.has_collect,
                  builder: i.document.create_user.username,
                  builderId: i.document.create_user.id
                })
              })
            }
          }).catch(e=>{
            if(e.response.status === 401){
              this.$message({message:'', type:'error'})
            }
          })
        } else if(this.type === 'build'){
          fetchMyDocs().then(res=>{
            if(res.status === 200){
              this.tableData = []
              res.data.forEach(i => {
                this.tableData.push({
                  docId: i.id,
                  docName: i.name,
                  builder: i.create_user.username,
                  builderId: i.create_user.id,
                  createTime: GetTime(i.create_time),
                  lastTime: GetTime(i.modify_time),
                  lastUser: i.last_modify_user?i.last_modify_user.username:"",
                  lastUserId: i.last_modify_user?i.last_modify_user.id:"",
                  isCollected: i.has_collect
                })
              })
            }
          }).catch(e=>{this.$message({message:e, type:'error'})})
        } else if(this.team){
          fetchTeamDocs(this.team).then(res=>{
            if(res.status === 200){
              this.tableData = []
              res.data.forEach(i=>{
                this.tableData.push({
                  docId: i.id,
                  docName: i.name,
                  builder: i.create_user_username,
                  builderId: i.create_user,
                  createTime: GetTime(i.create_time),
                  lastTime: GetTime(i.modify_time),
                  lastUser: i.last_modify_user_username,
                  lastUserId: i.last_modify_user,
                })
              })
            }
          }).catch(e=>{
            if(e.response&&e.response.status === 401){
              this.$message({message:'', type:'error'})
            }
          })
        } else if (this.type === 'dustbin'){
          fetchDustbin().then(res=>{
            if(res.status === 200){
              this.tableData =[]
              res.data.forEach(i=>{
                this.tableData.push({
                  docId: i.document.id,
                  docName: i.document.name,
                  deleteTime: GetTime(i.delete_time),
                  builder: i.document.create_user.username,
                  builderId: i.document.create_user.id,
                })
              })
            }
          }).catch(e=>this.$message({message: e.response.data, type: 'error'}))
        } else if (this.type === 'collection'){
          fetchCollections().then(res=>{
            if(res.status === 200){
              this.tableData = []
              res.data.forEach(i=>{
                this.tableData.push({
                  docId: i.id,
                  docName: i.name,
                  isCollected: i.has_collect,
                  builder: i.create_user.username,
                  builderId: i.create_user.id,
                  lastTime: GetTime(i.modify_time),
                  createTime: GetTime(i.create_time),
                  lastUser: i.last_modify_user?i.last_modify_user.username:"",
                  lastUserId: i.last_modify_user?i.last_modify_user.id:"",
                })
              })
            }
          }).catch(e=>this.$message({message: e.response.data, type: 'error'}))
        } else if (this.type === 'build_t'){
          fetchMyBuild().then(res=>{
            if(res.status === 200){
              this.tableData = []
              res.data.forEach(i => {
                this.tableData.push({
                  docId: i.id,
                  docName: i.name,
                  builder: i.create_user.username,
                  builderId: i.create_user.id,
                  createTime: GetTime(i.create_time),
                  lastTime: GetTime(i.modify_time),
                  lastUser: i.last_modify_user?i.last_modify_user.username:"",
                  lastUserId: i.last_modify_user?i.last_modify_user.id:"",
                  isCollected: i.has_collect
                })
              })
            }
          }).catch(e=>{this.$message({message:e, type:'error'})})
        }
      },
      createTimeFilter(timeName){
        let dateFilter = []
        let dates = []
        this.tableData.forEach(i =>{
          if(dates.length === 0 || dates.indexOf(GetTime(i[timeName],' '))==-1){
            dates.push(GetTime(i[timeName],' '))
          }
        })
        dates.forEach(i=>{
          dateFilter.push({
            text: i,
            value: i,
          })
        })
        return dateFilter
      },
      filterHandler(value, row, column) {
        const property = column['property'];
        return GetTime(row[property],' ') === value;
      },
      handleEdit(index, row) {//
        //this.Info = row//id
        this.getDocInfo(row)
        this.Dialog = true
      },
      Delete(id){
        let message = this.type === 'dustbin'?'':''
        this.$confirm(message).then(() => {
          if(this.type === 'dustbin'){
            deleteForever(id).then(res => {
              if(res.status === 204){
                this.init()
                this.$message({message:'!', type:'warning'})
              }
            }).catch(e=>{
              if(e.response.status === 401)
                this.$message({message:"", type:'error'})
            })
          } else {
            //
            deleteDoc(id).then(res => {
              if(res.status === 204){
                this.init()
                this.$message({message:'', type: 'info'})
              }
            }).catch(e=>{
              if(e.response.status === 401)
                this.$message({message:"", type:'error'})
            })
          }
          this.Dialog = false
        })
      },
      changeVisible(val, id){
        this.folderDialog = val
        this.getDocInfo(id)
      },
      openFolderDialog(id){
        this.docId = id
        let that = ''
        this.tableData.forEach(i=>{
          if(i.docId +''=== id+''){
            that = i.docName
            return ;
          }
        })
        this.NAME = that
        this.folderDialog = true
      },
      changeColl(id){
        let item = {};
        this.tableData.forEach(i => {
          if(i.docId+'' === id+''){
            item = i;
          }
        })
        if(item.isCollected){
          undoCollect(item.docId).then(res=>{
            if(res.status === 204){
              this.init()
              this.$message({message:'', type:'info'})
              item.isCollected = false
              return;
            }
          }).catch(e=>this.$message({message:e.response.data, type:'error'}))
        } else {
          doCollect(item.docId).then(res=>{
            if(res.status === 201){
              this.init()
              item.isCollected = true
              this.$message({message:'', type:'info'});
              return ;
            }
          }).catch(e=>{if(e.response.status === 400){
            this.$message({message:'', type:'error'})
          }})
        }
      },
      recover(id){
        undoDelete(id).then(res => {
          if(res.status === 200){
            this.init()
            this.$message({message:'', type:'info'})
            this.init()//
          }
        }).catch(e=>this.$message({message:e.response.data, type:'error'}))
      },
      toDoc(id){
        this.$router.push({name:'editorPage', params: {docId: id}})
      },
      toUser(id){
        this.$router.push({name:'PersonInfo', params:{personId: id}})
      },
    // table tr
      // eslint-disable-next-line no-unused-vars
      tableRowStyle({ row, rowIndex }) {
        if(row){
          return{
            backgroundColor: "whitesmoke"
          }
        }
      },
      // table header
      // eslint-disable-next-line no-unused-vars
      tableHeaderColor({ row, column, rowIndex, columnIndex }) {
        if (rowIndex === 0) {
          return 'background-color: whitesmoke;'
        }
      },
      setP(id){
        this.setId = id
        this.visi = this.Info.role
        this.isTeam = this.Info.isTeam
        this.NAME = this.Info.docName
        this.setV = true
      },
      changeP(val, id){
        this.setV = val
        this.getDocInfo(id)
      },
      shareDialog(id){
        console.log(id,'id',this.$cypher.encode(id+''))
        this.shareId = this.$cypher.encode(id+'')
        console.log(this.shareId)
        this.NAME = this.Info.docName
        this.shareV = true
      },
      changeShare(val){
        this.shareV = val
      },
      checkMove(item){
        let message = 'ta'
        this.$confirm(message).then(() => {
          removeCoworker(this.Info.id, item.userId).then(res=>{
            if(res.status === 200){
              this.Info.coworkers = []
              res.data.forEach((i, index) => {
                if(i.id+'' === this.userId){
                  this.isCo = true
                  this.role = i.role
                }
                if(index === 0){
                  this.Info.builder = i.username
                  this.Info.builderId = i.id
                }
                this.Info.coworkers.push({
                  userId: i.id,
                  userName: i.username,
                  userImg: i.head,
                  isBuilder: i.role+'' === 1+''? true:false,
                  role: i.role
                })
              })
              this.init()
              this.Dialog = false
              this.$message({message:'', type:'info'})
            } else {
              this.$message({message:'', type:'error'})
            }
          }).catch(e=>{
            if(e.response.status === 401){
              this.$message({message:'', type: 'error'})
            }
          })
        })
      },
      getDocInfo(id){
        fetchDocInfo(id,0).then(res=>{
          if(res.status === 200){
            this.Info.id = res.data.id
            this.Info.docName = res.data.name
            this.Info.role = res.data.role
            this.Info.isDoc = !res.data.type
            this.Info.isTeam = this.Info.isDoc && res.data.parent_doc
            this.Info.modify_time = GetTime(res.data.modify_time)
            this.Info.create_time = GetTime(res.data.create_time)
            fetchCoworkers(this.Info.id).then(res=>{
              if(res.status === 200){
                console.log(res.data)
                this.isCo = false;
                this.Info.coworkers = []
                res.data.forEach((i, index) => {
                  console.log(i)
                  if(i.id+'' === this.userId){
                    this.isCo = true
                    this.role = i.role
                  }
                  if(index === 0){
                    this.Info.builder = i.username
                    this.Info.builderId = i.id
                  }
                  this.Info.coworkers.push({
                    userId: i.id,
                    userName: i.username,
                    userImg: i.head,
                    isBuilder: i.role+'' === 1+''?true:false,
                    role:i.role
                  })
                })
                console.log(this.Info.coworkers)
              }
            }).catch(e => this.$message({message:e.response.data, type:'error'}))
          } else if(res.status === 204){
            this.$message({message:'', type: 'error'})
          }
        }).catch(e=>{
          this.$message({message:e.response.data, type: 'error'})
        })
      }
    }
  }
</script>

<style>
  .docList{
    height: 80vh;
  }
  .tableI{
    float: right;
    font-size: medium;
  }

  .tableI i{
    cursor:pointer;
    margin-right: 15px;
  }
  .el-table-filter__checkbox-group .el-checkbox:last-child {
     margin-bottom: 8px;
  }
  .el-table-filter {
    border: 1px solid #EBEEF5;
    border-radius: 2px;
    background-color: #fbfbfb;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    box-sizing: border-box;
    margin: 2px 0;
  }

  .tabley .el-table__empty-block{
    background-color: whitesmoke;
  }

  .el-table td.gutter, .el-table th.gutter {
    width: 15px;
    background-color: whitesmoke;
    border-right-width: 0;
    border-bottom-width: 0;
    padding: 0;
  }
  .tabley .el-table__body-wrapper {
    overflow: hidden;
    position: relative;
    background-color: whitesmoke;
  }
</style>